<template>
    <div>
        <h3>src/components/Admin.vue</h3>
                {{menu}}

        <button @click="handleMenu(1)">1</button>
        <button @click="handleMenu(2)">2</button>
        <button @click="handleMenu(3)">3</button>
        <button  @click="handleCount">1 </button>
        <menu-1 title= "111" v-if="menu.code ===1"></menu-1>
        <menu-2 title= "2" @handleCount="handleCount" v-if="menu.code ===2"></menu-2>
        <menu-3 :title= count.value aaaa="" @handleCount="handleCount" v-if="menu.code ===3" ></menu-3>
        
        <!-- title = "bbb"  
        :title = " state.bbb" :  , :     -->
        
    </div>
</template>

<script>
import  {reactive} from 'vue';
import Menu1 from './admin/menu-1.vue';
import Menu2 from './admin/menu-2.vue';
import Menu3 from './admin/menu-3.vue';

export default {
    components:{
        Menu1, Menu2, Menu3
    },

    setup () {

        //state 
        let menu = reactive({
            code: 1
        })

        const count = reactive(
            {value : 12341234}
        )

        //handleMenu 
        const handleMenu = (idx) => {
            console.log(idx)
            menu.code = idx;
          
        }

        const handleCount = () => {
            count.value++;
            console.log("Admin.vue=>",count.value)

        }
        
        return {menu,handleMenu, handleCount, count}
    }
}
</script>

