<template>
    <div class="style1">
        <h3>src/components/Join.vue</h3>
        <input type="text" ref="userid" @keyup="handleEmailCheck" v-model="state.userid">
        <div>{{state.userMailChk}}</div>
        <input type="password" v-model="state.userpw"><br />
        <input type="password" v-model="state.userpw1"><br />
        <input type="text" v-model="state.username"><br />
        
        
        :
        <select v-model="state.userrole">
            <option value="CUSTOMER"></option>
            <option value="SELLER"></option>
        </select><br/>
        <button @click="handleJoin"></button>
        {{state}}
    </div>
</template>

<script>
import {reactive , ref} from 'vue';
import axios from 'axios'
import { useRouter } from 'vue-router';

export default {
    setup () {
        const router = useRouter();
        //High  :   :   
        //    
        //   reactive  .
        const state = reactive({
            userid  : "",
            userpw  : '',
            userpw1 : '',
            username: '',
            userMailChk : '',
            userrole: 'CUSTOMER'
        })
        //low:  :  
        //   , 
        //focus  , ,   
        //  reactive    , (v-model )
        const userid = ref(null);   //   bbb  
        const userpw = ref(null);
        const userpw1 = ref(null);
        const username = ref(null);
        console.log(userid)


        const validEmail = (email) => {
            //
            var re = /[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]*$/i;
            return re.test(email);
        }

        const handleEmailCheck = async()=>{
            if(validEmail(state.userid)){
                const url = `/member/emailcheck?email=${state.userid}`;
                const headers = {"Content-Type": "apllication/json"};
                const response = await axios.get(url, {headers});
                console.log(response);
                if(response.data.status ===200){    
                    if(response.data.result ===1){
                        state.userMailChk = ''
                    }
                    else{
                        state.userMailChk = ''
                    }
                }
                else{
                    state.userMailChk = ''
                }
            }
            

        }
        
        const handleJoin = async() =>{
            if(state.userid ===''){
                alert('ID ');
                userid.value.focus();
                return false;
            }
            if(state.userpw ===''){
                alert(' ');
                return false;
            }
            if(state.userpw1 ===''){
                alert('');
                return false;
            }
            if(state.userpw1 !==state.userpw){
                alert(' ');
                return false;
            }
            
            if(state.username ===''){
                alert(' ~');
                return false;
            }
            if(state.userMailChk !== ''){
                alert('   ');
                return false;
            }
        
            //      

            const url = "/member/insert";
            const headers = {"Content-Type": "application/json"};
            const body = {
                email : state.userid,
                pw    : state.userpw,
                name  : state.username,
                role  : state.userrole,
            }
            const response = await axios.post(url, body, {headers:headers});
            console.log(response.data);
            if(response.data.status === 200){
                alert(' ')
                router.push({name : "Login"})
            }
            
        }

        return {state, handleJoin, userid, userpw, userpw1, 
        username, handleEmailCheck,validEmail, router}
    }
}
</script>

