//npm install vuex@next --save
import {createStore} from 'vuex';
import axios from 'axios';



//    
//props emit  .

const stores = createStore({

    //  
    state : {
        // menu1 : sessionStorage.getItem("CURL1"), //index   
        menu   : sessionStorage.getItem("CURL"), //index   
        logged : false,
        uid    : '',  //    
        uname  : '',  //  
        urole  : '',
        token  : '',  //    
        
    },

    // (getter)
    getters:{
        getmenu(state){
            return state.menu
        },
        getLogged(state){
            return state.logged;
        },
        getUid(state){
            return state.uid;
        },
        getUname(state){
            return state.uname;
        },
        getUrole(state){
            return state.urole;
        }
    },

    // (mutation) :  
    mutations: {
        setmenu(state,value){
            state.menu = value    
        },
        setLogged(state,value){
            state.logged = value
        },
        setUid(state,value){
            state.uid = value
        },
        setUname(state,value){
            state.uname = value
        },
        setUrole(state,value){
            state.urole = value
        },
    },

    //(action) :   ,    
    actions: {
        async handleData(context , payload){
            console.log(payload)
            const token = sessionStorage.getItem("TOKEN");
            if(typeof token !== 'undefined' && token !==null){
                const url = `/member/validation`;
                const headers = {
                "Content-Type": "application/json",
                "token" : token,
                };
                const response = await axios.get(url, {headers})
                if(response.data.status ===200){
                    context.commit("setUid", response.data.uid);
                    context.commit("setUname", response.data.uname);
                    context.commit("setUrole", response.data.urole);
                    context.commit("setLogged", true);
                }
                else{
                    //    
                    context.commit("setLogged", false);
                }
            }
            
        }
    }
    
})

export default stores